body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    margin: 0 auto;
    display: flex;
    flex-flow: column;
    text-align: center;
    align-items: center;
}

.reminder-form {
    padding: 5px;
}

.title {
    text-align: center;
    font-size: 26px;
}

.form-group {
    margin: 5px;
    padding: 5px;
}

.form-control {
    margin-right: 10px;
}

.list-group-item {
    text-align: left;
}

.list-item {
    display: inline-block;
}

.delete-button {
    float: right;
    margin-left: 10px;
    cursor: pointer;
}

.clearAll {
    margin: 16px;
}
